import React, { useState } from 'react';
const data = require('../stub/data-sample.json');

const Index = () => {
    const [ searchTerm, updateSearchTerm ] = useState('');
    
    let filterData = data.report.foods.filter( item => item.name.toLowerCase().indexOf(searchTerm.toLowerCase()) !== -1);
    console.log('Searched data: ', filterData);
    return(
        <div>
            <h1>Main page</h1>
            <p>Some paragraph</p>
            <input placeholder="Search here..." onKeyUp= { (e) => updateSearchTerm( e.target.value )  } />
            <p>Search term should reflect here: { searchTerm }</p>
        </div>
    )
}

export default Index;